body {
  background-image: url(../images/bg.jpg);
}
.App {
  text-align: center;
  padding: 20px;
}

h1 {
  color: aqua;
  margin-bottom: 20px;
}

p {
  color: #007bff;
}

.search-container {
  margin-bottom: 20px;
}

input[type='text'] {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  margin: 10px;
}

button {
  margin: auto;
  padding: 8px 16px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movie-card {
  width: 200px;
  margin: 10px;
  padding: 10px;
  background-color: darkslategrey;
  border-radius: 5px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
  cursor: pointer;
}

.movie-card:hover {
  transform: scale(1.05);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.movie-card img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.movie-card h3 {
  margin-bottom: 5px;
}

.movie-card p {
  margin-bottom: 5px;
}

.dark-mode-toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
}

.dark-mode-toggle input[type='checkbox'] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.dark-mode-toggle label {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  background-color: #ccc;
  border-radius: 20px;
  cursor: pointer;
}

.dark-mode-toggle label:before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 50%;
  transition: transform 0.2s;
}

.dark-mode-toggle input:checked + label {
  background-color: #007bff;
}

.dark-mode-toggle input:checked + label:before {
  transform: translateX(20px);
}

.dark-mode {
  background-color: #222;
  color: #fff;
}

.dark-mode .movie-card {
  background-color: #333;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

.dark-mode .placeholder-image {
  background-color: #555;
}
